.experience_col {
  text-align: center;
  color: turquoise;
  background-color: black;
  border-radius: 5rem;
  margin-bottom: 7rem;
  box-shadow: 0px 0px 4px 0px rgba(0, 0, 0, 1);
}
.title {
  color: turquoise;
}

.experience {
  background-image: url(https://images.unsplash.com/photo-1598029018879-7c25e7f252be?ixid=MXwxMjA3fDB8MHxzZWFyY2h8NTN8fHBhcmFsbGF4JTIwZWZmZWN0fGVufDB8fDB8&ixlib=rb-1.2.1&auto=format&fit=crop&w=500&q=60);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  padding-top: 4rem;
}

.paragraph {
  width: 90%;
  text-align: start;
  margin: 2rem 2rem;
}
.paragraph_body {
  color: turquoise;
  font-size: 1.2rem;
}

.btn2,
.btn2:visited {
  background-color: turquoise;
  margin-top: 1rem;
  font-size: 1.2rem;
  height: 4rem;
  width: 15rem;
  margin-bottom: 3rem;
}
.btn2:hover {
  background-color: rgba(64, 224, 208, 0.404);
  margin-top: 1rem;
  font-size: 1.2rem;
  height: 4rem;
  width: 15rem;
  border: none;
}

.desg_ans {
  font-weight: 500;
  background: -webkit-linear-gradient(rgb(0, 247, 255), rgb(105, 167, 65));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}